<template>
    <div class="echarts">
        <h3>echarts</h3>
        <div ref="echarts" style="height:400px;"></div>
        <div ref="echarts2" style="height:400px;"></div>
        <div ref="echarts3" style="height:400px;"></div>
        <router-link to="/bdmap"><input type="button" value="" class="search" ></router-link>
        
    </div>
</template>
<script>
import echarts from 'echarts'
import { setTimeout } from 'timers';
export default {
    mounted(){
        this.myChart = echarts.init(this.$refs.echarts); //
        // 
        var option = {
            title: {
                text: 'ECharts '
            },
            tooltip: {},
            legend: {
                data:['','']
            },
            xAxis: {
                data: ["","","","","",""]
            },
            yAxis: {},
            series: [{
                name: '',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            },{
                name: '',
                type: 'bar',
                data: [5, 11, 36, 10, 10, 20]
            }]
        };
            
        // 
        this.myChart.setOption(option);
        this.myChart.on('click', function (params) {
            window.open('https://www.baidu.com/s?wd=' + encodeURIComponent(params.name));
        });
// -----------------------------------------------------

        this.myChart2 = echarts.init(this.$refs.echarts2); //
        // 
        
        var option2 = {
            backgroundColor: '#2c343c',
            series : [
        {
            name: '',
            type: 'pie',
            radius: '55%',
            data:[
                {value:235, name:''},
                {value:274, name:''},
                {value:310, name:''},
                {value:335, name:''},
                {value:400, name:''}
            ],
            roseType: 'angle',
            itemStyle: {
                normal: {
                    shadowBlur: 20,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            }
        }
    ]
        };
        // 
        this.myChart2.setOption(option2);

// -----------------------------------------------------------

        this.myChart3 = echarts.init(this.$refs.echarts3); //
        this.myChart3.setOption({
            title: {
                text: ''
            },
            tooltip: {},
            legend: {
                data:['']
            },
            xAxis: {
                data: []
            },
            yAxis: {},
            series: [{
                name: '',
                type: 'bar',
                data: []
            }]
        });
        axios({
            url:'/data/echarts.json'
        }).then(
            res=>{
                setTimeout(()=>{this.myChart3.setOption(res.data);},1000)
            }
        )
    }
}
</script>
<style scoped>
    .search{
        margin-bottom: 1rem;border: 1px solid #000; color: cornflowerblue;line-height: .5rem;border-radius: .05rem;padding: 0 .05rem;cursor: pointer;
    }
</style>
